import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TabsComponent } from './tabs.component';
import { HomePage } from '../home/home.page'
import { JourneyPlannerPage } from '../journey-planner/journey-planner.page';
import { MapPage } from '../map/map.page';
const routes: Routes = [
  {
    path: 'tabs',
    component: TabsComponent,
    children: [
      {
        path: '',
        redirectTo: '/tabs/(home:home)',
        pathMatch: 'full',
      },
      {
        path: 'home',
        outlet: 'home',
        component: HomePage,
      },
      {
        path: 'journey-planner',
        outlet: 'journey-planner',
        component: JourneyPlannerPage,
      },
      {
        path: 'map',
        outlet: 'map',
        component: MapPage,
      }
    ]
  },
  {
    path: '',
    redirectTo: '/tabs/tabs',
    pathMatch: 'full',
  },
];
// { path: 'map', loadChildren: './map/map.module#MapPageModule' },
// { path: 'database', loadChildren: './database/database.module#DatabasePageModule' },
// { path: 'journey-planner', loadChildren: './journey-planner/journey-planner.module#JourneyPlannerPageModule' },
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TabsRoutingModule { }
